body {
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  width: 100%;
  background-color: #F5F5F5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

.section {
  background-color: #FFF;
  margin: 0 0 20px 0;
  padding: 10px 5px 20px 5px;
  border-top: 10px solid #000;
}

h1 {
  font-weight: bold;
  font-family: Nunito;
}

h2 {
  font-weight: bold;
  font-family: Nunito;
  margin: 0;
  padding: 0;
}

.nav-tabs {
  border-bottom: 0px solid #d45500;
  margin: 0;
  padding: 0;
}

link, .nav-tabs .nav-link {
  cursor: pointer;
  border: 0;
  margin-bottom: 10px;
  padding-top: 5px;
  padding-bottom: 2px;
  border-bottom: 5px solid #FFF;
}

.nav-tabs .nav-link.active {
  font-weight: bold;
  cursor: default;
  border-bottom-color: #000;
}

link:focus, .nav-tabs .nav-link:hover {
  border-color: #000;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#navbar {
  background-color: #2C3531;
  width: 100%;
  margin: 0 0 20px 0;
}

#navbar h1 {
  margin: 0;
  padding: 0;
  color: #FFF;
}

.timeline_axis {
  stroke-width: 2;
}

.timeline_axis .domain {
  stroke-width: 2;
}

.timeline_axis text {
  fill: #000;
  font-size: 11px;
}

.timeline_axis .tick:nth-child(2n) text {
  visibility: hidden;
}

.timeline_line {
  stroke: #BBB;
  stroke-width: 2;
}

.timeline_circle {
  cursor: pointer;
  fill: #000;
}

.timeline_tooltip {
  position: absolute;
  text-align: left;
  font-size: 12px;
  padding: 5px;
  background: lightsteelblue;
  pointer-events: none;
}

.hide {
  display: none;
}

#multiline {
  position: relative;
}

.multiline {
  fill: none;
  stroke-width: 2;
}

#multiline .grid line {
  opacity: 0.2;
}

.multiline_tooltip {
  position: absolute;
  padding: 5px;
  text-align: left;
  font-size: 12px;
  pointer-events: none;
  background-color: #FFF;
}

.multiline_circle {
  display: inline-block;
  float: left;
  margin: 0 5px 0 0;
  width: 15px;
  height: 15px;
  border-radius: 100px;
}

.loading {
  text-align: center;
  background-color: #FFF;
  min-height: 200px
}

#scatter {
  position: relative;
}

#scatter .grid line{
  opacity: 0.2;
}

.scatter_circle {
  cursor: pointer;
}

.scatter_tooltip {
  position: absolute;
  padding: 5px 5px 5px 5px;
  text-align: left;
  font-size: 12px;
  pointer-events: none;
  background-color: #FFF;
  border-left-style: solid;
  border-left-width: 7px;
  max-width: 300px;
}

.sparkline .line {
  fill: none;
  stroke-width: 1.2;
}

.sparkline .area {
  opacity: 0.2;
}

/* .sparkline_axis .tick:nth-child(2) text {
  display: none;
} */

.sparkline_axis .tick text{
  font-weight: 0;
  stroke: #000;
  font-size: 11px;
  stroke-width: 0;
}

.sparkline_axis path {
  fill: none;
  stroke: #000;
  stroke-width: 1;
}

.sparkline_axis .tick {
  fill: none;
  stroke: #000;
  stroke-width: 1;
}

.item {
  border-left-width: 7px;
  border-left-style: solid;
  margin: 0 0 5px 0;
  padding: 5px 5px 5px 10px;
  background-color: #FFF;
}

.item p {
  margin: 0;
  padding: 0;
}

.item a {
  color: #000;
}

.item_title {
  font-weight: 600;
}

.item_sub {
  font-size: 14px;
}

.badge {
  font-family: 'PT Sans';
  color: #FFF;
  padding: 2px 5px 2px 5px;
  font-size: 14px;
  margin: 0 7px 0 0;
}

